import React, { Component } from 'react';
import "../css/CollabNames.css";
import { performListPost } from '../actions/listActions';
import { LISTS_URL } from '../listrUrls';
import { connect } from 'react-redux';
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Switch from '@material-ui/core/Switch';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';

import withMobileDialog from '@material-ui/core/withMobileDialog';

class NewListForm extends Component {

  constructor(props){
    super(props);
    this.state = {
      collabs: [],
      listTitle: null,
      open: false,
      currentCollab: "",
      readOnly: false
    }
    this.addItem = this.addItem.bind(this);
    this.listCollabs = this.listCollabs.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.createNewList = this.createNewList.bind(this);
  }

  // LIST COLLABORATORS
  deleteItem(key){
    var filteredItems = this.state.collabs;
    var ind = filteredItems.indexOf(key);
    if(ind > -1){
      filteredItems.splice(ind, 1);
      this.setState({
        collabs:filteredItems
      });
    }
  }
  addItem(e){
    if(this.state.currentCollab !== ""){
      var newCollab = this.state.currentCollab
      };

      this.setState((prevState) => {
        return {
          collabs: prevState.collabs.concat(newCollab)
        };
      });
      this.setState({currentCollab: ""});
      e.preventDefault();
    }
  _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.addItem(e);
    }
  }
  listCollabs(collab){
    return <li onClick={() => this.deleteItem(collab)} >@{collab}</li>
  }
  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value});
  }

  handleClickOpen = (e) => {
    this.setState((prevState) => ({
      open: true,
    }));
  }

  handleClose = (e) => {
    this.setState((prevState) => ({
      open: false,
    }));
  }

  toggleReadOnly = (e) => {
    this.setState({readOnly: !this.state.readOnly});
  }

  //LIST POSTING
  createNewList(e){
    e.preventDefault();
    const listData = {
      "name": this.state.listTitle,
      "static_id": this.makeid(),
      "collabs": this.state.collabs,
      "readOnly": this.state.readOnly
    }
    this.props.performListPost(LISTS_URL, listData, this.props.token);
    this.handleClose();
    this.setState({collabs: [], currentCollab: ""})
  }
  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 6; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }


  render () {
    const { fullScreen } = this.props;
    var collabs = this.state.collabs.map(this.listCollabs);
    var style;
    var inline={
      display: "inline"
    }
    if(this.state.currentCollab !== ''){
      style={
      }
    }else{
      style={
        visibility: "hidden"
      }
    }
    return(
      <div>
        <Button style={{marginLeft: "10px"}} variant="contained" color="primary" onClick={this.handleClickOpen}>
          New Pad
          <i className="fa fa-plus" style={{marginLeft:10, marginTop:-2}}></i>
        </Button>
        <Dialog
          fullScreen={fullScreen}
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Create New Pad</DialogTitle>
          <DialogContent>
            <TextField
              name="collabs"
              type="text"
              id="listTitle"
              label="Name Your Pad"
              autoComplete='off'
              value={this.state.listTitle}
              onChange={(e) => this.handleChange(e)}
              variant='outlined'
              style={{marginTop:10}}
              fullWidth
              required
            />
            <Grid container key={this.props.data}>
              <Grid item xs={12} sm={12} md={9} lg={9} style={{marginTop:10}}>
                <TextField
                  name="collabs"
                  type="text"
                  id="currentCollab"
                  label="Add New Collabs (By Username)"
                  autoComplete='off'
                  value={this.state.currentCollab}
                  onChange={(e) => this.handleChange(e)}
                  onKeyPress={this._handleKeyPress}
                  variant='outlined'
                  style={{marginTop:10}}
                  fullWidth
                />
              </Grid>
              <Grid style={{marginLeft: 'auto', marginRight:'auto', marginTop:30}} item xs={12} sm={12} md={2} lg={2}>
                <Button onClick={(e) => this.addItem(e)} color="primary" variant="raised" fullWidth>
                  Add
                </Button>
              </Grid>
              <br />
              <Grid item xs={12} sm={12} md={12} lg={12}>
                <p >Press 'Enter' after each username to add. You can add multiple collabs</p>
                <div className="collabList">
                {collabs}
                </div>
              </Grid>
          </Grid>
            <p style={inline}>Like Only</p>
            <Switch
              checked={this.state.readOnly}
              onChange={this.toggleReadOnly}
              value="readOnly"
              color="primary"
            />
          <br />
          <small>You can add items too this list, but collabs can only like items.</small>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Cancel
            </Button>
            <Button onClick={this.createNewList} color="primary" variant="raised">
              Create List
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }

}


NewListForm.proptypes = {
  performListPost: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  token: state.users.token,
});

var ToExport = withMobileDialog()(NewListForm);
export default connect(mapStateToProps, { performListPost })(ToExport);



